import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "../Styles/AdminPage.css";
import NavigationBar from "../NavigationBar";
import Footer from "../Footer";

const AdminPage = () => {
  const navigate = useNavigate();

  useEffect(() => {
    document.title = "Admin Sayfas";
  }, []);

  useEffect(() => {
    const userType = localStorage.getItem("userType");

    if (userType !== "admin") {
      navigate("/");
    }
  }, [navigate]);

  const formatNumber = (number) => {
    const formattedNumber = number
      .toFixed(2)
      .replace(".", ",")
      .replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    return formattedNumber;
  };
  const [applicationDataArray, setApplicationDataArray] = useState([]);

  useEffect(() => {
    fetch("http://127.0.0.1:5000/get_loan_data")
      .then((response) => response.json())
      .then((data) => {
        const processedData = data.map((item) => ({
          ...item,
          KrediMiktar: parseFloat(item.KrediMiktar) || 0,
          KrediVadesi: parseInt(item.KrediVadesi) || 0,
          FaizOran: parseFloat(item.FaizOran) || 0,
          AylkTaksit: parseFloat(item.AylkTaksit) || 0,
          ToplamKrediMaliyeti: parseFloat(item.ToplamKrediMaliyeti) || 0,
          ToplamFaiz: parseFloat(item.ToplamFaiz) || 0,
          YllkMaliyetOran: parseFloat(item.YllkMaliyetOran) || 0,
          BankaTahsiscreti: parseFloat(item.BankaTahsiscreti) || 0,
          Ekspertizcreti: parseFloat(item.Ekspertizcreti) || 0,
          potekcreti: parseFloat(item.potekcreti) || 0,
        }));
        setApplicationDataArray(processedData);
      })
      .catch((error) => {
        console.error("Veri ekme hatas:", error);
      });
  }, []);
  const handleAcceptRow = async (id) => {
    try {
      const response = await fetch(
        `http://127.0.0.1:5000/accept_loan_application/${id}`,
        {
          method: "POST",
        }
      );
      const data = await response.json();

      if (data.message) {
        const updatedDataArray = applicationDataArray.map((item) =>
          item.ID === id ? { ...item, KrediDurumu: 1 } : item
        );
        setApplicationDataArray(updatedDataArray);
      } else {
        console.error("Acceptance error:", data.error);
      }
    } catch (error) {
      console.error("Acceptance error:", error);
    }
  };
  const handleDeleteRow = async (id) => {
    try {
      const response = await fetch(
        `http://127.0.0.1:5000/delete_loan_data/${id}`,
        {
          method: "DELETE",
        }
      );
      const data = await response.json();

      if (data.message) {
        const updatedDataArray = applicationDataArray.filter(
          (item) => item.ID !== id
        );
        setApplicationDataArray(updatedDataArray);
      } else {
        console.error("Silme hatas:", data.error);
      }
    } catch (error) {
      console.error("Silme hatas:", error);
    }
  };

  const [interestRate, setInterestRate] = useState(0);

  useEffect(() => {
    const savedInterestRate = localStorage.getItem("interestRate");
    if (savedInterestRate) {
      setInterestRate(parseFloat(savedInterestRate));
    }
  }, []);

  const handleInterestRateChange = (event) => {
    const newInterestRate = parseFloat(event.target.value);
    setInterestRate(newInterestRate);
  };

  const handleSaveButtonClick = async () => {
    localStorage.setItem("interestRate", interestRate.toString());

    try {
      const response = await fetch(
        "http://127.0.0.1:5000/update_interest_rate",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ interestRate }),
        }
      );

      if (response.ok) {
        alert("Faiz oran kaydedildi!");
      } else {
        const errorData = await response.json();
        alert("Faiz oran kaydedilirken hata: " + errorData.error);
      }
    } catch (error) {
      console.error("Faiz oran kaydedilirken hata:", error);
    }
  };

  return (
    <div>
      <NavigationBar />
      <div className="admin-page-container">
        <div className="admin-form-container">
          <h1 className="admin-centered-text">ADMIN</h1>
          <div className="admin-label-input-container">
            <label>Yeni Faiz Oran:</label>
            <input
              type="number"
              step="0.01"
              placeholder="Faiz Oran"
              onChange={handleInterestRateChange}
            />
          </div>
          <div>
            <p>Aktif Faiz Oran: %{interestRate}</p>{" "}
          </div>
          <div className="admin-button-container">
            <button onClick={handleSaveButtonClick}>KAYDET</button>
          </div>
        </div>
        <div>
          <h2>Kredi Bavurular</h2>
          {applicationDataArray.length > 0 ? (
            <table>
              <thead>
                <tr>
                  <th>ID</th>
                  <th>Kredi Tutar</th>
                  <th>Kredi Vadesi</th>
                  <th>Faiz Oran</th>
                  <th>Aylk Taksit</th>
                  <th>Toplam deme</th>
                  <th>Toplam Faiz</th>
                  <th>Yllk Maliyet Oran</th>
                  <th>Banka Tahsis creti</th>
                  <th>Ekspertiz creti</th>
                  <th>potek creti</th>
                  <th>User ID</th>
                  <th>Kredi Durumu</th>
                  <th>Bavuru Onay</th>
                  <th>Bavuru Sil</th>
                </tr>
              </thead>

              <tbody>
                {applicationDataArray.map((data) => (
                  <tr key={data.ID}>
                    <td>{data.ID}</td>
                    <td>
                      {data.KrediMiktar !== null
                        ? formatNumber(data.KrediMiktar) + " TL"
                        : ""}
                    </td>
                    <td>
                      {data.KrediVadesi !== null
                        ? data.KrediVadesi + " Ay"
                        : ""}
                    </td>
                    <td>
                      {data.FaizOran !== null
                        ? data.FaizOran.toFixed(2) + "%"
                        : ""}
                    </td>
                    <td>
                      {data.AylkTaksit !== null
                        ? formatNumber(data.AylkTaksit) + " TL"
                        : ""}
                    </td>
                    <td>
                      {data.ToplamKrediMaliyeti !== null
                        ? formatNumber(data.ToplamKrediMaliyeti) + " TL"
                        : ""}
                    </td>
                    <td>
                      {data.ToplamFaiz !== null
                        ? formatNumber(data.ToplamFaiz) + " TL"
                        : ""}
                    </td>
                    <td>
                      {data.YllkMaliyetOran !== null
                        ? data.YllkMaliyetOran.toFixed(2) + "%"
                        : ""}
                    </td>
                    <td>
                      {data.BankaTahsiscreti !== null
                        ? formatNumber(data.BankaTahsiscreti) + " TL"
                        : ""}
                    </td>
                    <td>
                      {data.Ekspertizcreti !== null
                        ? formatNumber(data.Ekspertizcreti) + " TL"
                        : ""}
                    </td>
                    <td>
                      {data.potekcreti !== null
                        ? formatNumber(data.potekcreti) + " TL"
                        : ""}
                    </td>
                    <td>{data.UserID !== null ? data.UserID : ""}</td>
                    <td>
                      <div
                        className={`status-indicator ${
                          data.KrediDurumu === 1 ? "approved" : "pending"
                        }`}
                      ></div>
                    </td>
                    <td>
                      <button
                        className="accept-button"
                        onClick={() => handleAcceptRow(data.ID)}
                        disabled={data.KrediDurumu === 1}
                      >
                        Onayla
                      </button>
                    </td>
                    <td>
                      <button
                        className="delete-button"
                        onClick={() => handleDeleteRow(data.ID)}
                      >
                        Sil
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          ) : (
            <p>Kredi bavurusu bulunmuyor.</p>
          )}
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default AdminPage;
