import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "../Styles/Login.css";
import Footer from "../Footer";

const Login = () => {
  const navigate = useNavigate();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [userType, setUserType] = useState("user");
  const [loginSuccess, setLoginSuccess] = useState(true);

  useEffect(() => {
    document.title = "Kayt Ol - Giri Yap";
  }, []);

  const handleLogin = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch("http://127.0.0.1:5000/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          username,
          password,
          userType,
        }),
      });

      const data = await response.json();
      console.log(data);

      if (response.ok && data.authenticated) {
        console.log("Giri baarl");

        localStorage.setItem("userType", userType);
        localStorage.setItem("userId", data.userId);
        localStorage.setItem("username", data.username);

        if (userType === "admin") {
          navigate("/admin");
        } else {
          navigate("/kredihesaplama");
        }
      } else {
        console.log("Giri baarsz");
        localStorage.removeItem("userType");
        localStorage.removeItem("userId");
        setLoginSuccess(false);
      }
    } catch (error) {
      console.error("Giri srasnda hata:", error);
    }
  };
  const handleRegistration = async () => {
    try {
      const response = await fetch("http://127.0.0.1:5000/register", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          username,
          password,
        }),
      });

      if (response.ok) {
        console.log("Kayt baarl!");
        window.alert("Kayt baarl! Giri yapabilirsiniz.");
      } else {
        console.log("Kayt baarsz");
        window.alert("Kayt baarsz. Ltfen tekrar deneyin.");
      }
    } catch (error) {
      console.error("Kayt srasnda hata:", error);
      window.alert("Kayt srasnda bir hata olutu. Ltfen tekrar deneyin.");
    }
  };

  const toggleUserType = () => {
    setUserType(userType === "user" ? "admin" : "user");
  };

  return (
    <div className="login-container">
      <div className="login-form">
        <h1>Giri Yap</h1>

        <form onSubmit={handleLogin}>
          <div className="input-group">
            <label>Kullanc Ad:</label>
            <input
              type="text"
              value={username}
              placeholder="Kullanc Ad"
              onChange={(e) => setUsername(e.target.value)}
            />
          </div>

          <div className="input-group">
            <label>ifre:</label>
            <input
              type="password"
              value={password}
              placeholder="ifre"
              onChange={(e) => setPassword(e.target.value)}
            />
          </div>

          <div className="user-type-toggle">
            <label>Admin Girii: </label>
            <input
              type="checkbox"
              id="user-type-toggle"
              className="toggle-switch"
              checked={userType === "admin"}
              onChange={toggleUserType}
            />
            <label htmlFor="user-type-toggle" className="toggle-label">
              <span className="toggle-inner"></span>
              <span className="toggle-switch"></span>
            </label>
          </div>

          <button type="submit">Giri Yap</button>

          <button type="button" onClick={handleRegistration}>
            Kayt Ol
          </button>

          {!loginSuccess && (
            <p className="warning-message">
              Kullanc ad, ifre veya tr hatal.
            </p>
          )}
        </form>
      </div>
      <Footer />
    </div>
  );
};

export default Login;
