import React, { useState, useEffect } from "react";
import "../Styles/BavurularmPage.css";
import Footer from "../Footer";
import NavigationBar from "../NavigationBar";

const BavurularmPage = () => {
  const [loanData, setLoanData] = useState([]);
  const userId = localStorage.getItem("userId");

  const formatNumber = (number) => {
    const formattedNumber = number
      .toFixed(2)
      .replace(".", ",")
      .replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    return formattedNumber;
  };

  useEffect(() => {
    fetch(`http://127.0.0.1:5000/get_loan_data?userId=${userId}`)
      .then((response) => {
        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }
        return response.json();
      })
      .then((data) => {
        const processedData = data.map((item) => ({
          ...item,
          KrediMiktar: parseFloat(item.KrediMiktar) || 0,
          KrediVadesi: parseInt(item.KrediVadesi) || 0,
          FaizOran: parseFloat(item.FaizOran) || 0,
          AylkTaksit: parseFloat(item.AylkTaksit) || 0,
          ToplamKrediMaliyeti: parseFloat(item.ToplamKrediMaliyeti) || 0,
          ToplamFaiz: parseFloat(item.ToplamFaiz) || 0,
          YllkMaliyetOran: parseFloat(item.YllkMaliyetOran) || 0,
          BankaTahsiscreti: parseFloat(item.BankaTahsiscreti) || 0,
          Ekspertizcreti: parseFloat(item.Ekspertizcreti) || 0,
          potekcreti: parseFloat(item.potekcreti) || 0,
        }));
        setLoanData(processedData);
      })
      .catch((error) => console.error("Error fetching loan data:", error));
  }, [userId]);

  const handleDeleteLoanData = (dataId) => {
    fetch(`http://127.0.0.1:5000/delete_loan_data/${dataId}`, {
      method: "DELETE",
    })
      .then((response) => response.json())
      .then(() => {
        setLoanData((prevData) =>
          prevData.filter((data) => data.ID !== dataId)
        );
      })
      .catch((error) => console.error("Error deleting loan data:", error));
  };

  return (
    <div>
      <NavigationBar />
      <div className="Bavurularm-Page">
        <h2>Kredi Bavurularm</h2>
        {loanData.length > 0 ? (
          <table>
            <thead>
              <tr>
                <th>ID</th>
                <th>Kredi Tutar</th>
                <th>Kredi Vadesi</th>
                <th>Faiz Oran</th>
                <th>Aylk Taksit</th>
                <th>Toplam deme</th>
                <th>Toplam Faiz</th>
                <th>Yllk Maliyet Oran</th>
                <th>Banka Tahsis creti</th>
                <th>Ekspertiz creti</th>
                <th>potek creti</th>
                <th>Kredi Durumu</th>
                <th>Bavuru Sil</th>
              </tr>
            </thead>

            <tbody>
              {loanData.map((data) => (
                <tr key={data.ID}>
                  <td>{data.ID}</td>
                  <td>
                    {data.KrediMiktar !== null
                      ? formatNumber(data.KrediMiktar) + " TL"
                      : ""}
                  </td>
                  <td>
                    {data.KrediVadesi !== null ? data.KrediVadesi + " Ay" : ""}
                  </td>
                  <td>
                    {data.FaizOran !== null
                      ? data.FaizOran.toFixed(2) + "%"
                      : ""}
                  </td>
                  <td>
                    {data.AylkTaksit !== null
                      ? formatNumber(data.AylkTaksit) + " TL"
                      : ""}
                  </td>
                  <td>
                    {data.ToplamKrediMaliyeti !== null
                      ? formatNumber(data.ToplamKrediMaliyeti) + " TL"
                      : ""}
                  </td>
                  <td>
                    {data.ToplamFaiz !== null
                      ? formatNumber(data.ToplamFaiz) + " TL"
                      : ""}
                  </td>
                  <td>
                    {data.YllkMaliyetOran !== null
                      ? data.YllkMaliyetOran.toFixed(2) + "%"
                      : ""}
                  </td>
                  <td>
                    {data.BankaTahsiscreti !== null
                      ? formatNumber(data.BankaTahsiscreti) + " TL"
                      : ""}
                  </td>
                  <td>
                    {data.Ekspertizcreti !== null
                      ? formatNumber(data.Ekspertizcreti) + " TL"
                      : ""}
                  </td>
                  <td>
                    {data.potekcreti !== null
                      ? formatNumber(data.potekcreti) + " TL"
                      : ""}
                  </td>
                  <td>
                    <div
                      className={`status-indicator ${
                        data.KrediDurumu === 1 ? "approved" : "pending"
                      }`}
                    ></div>
                  </td>
                  <td>
                    <button
                      className="delete-button"
                      onClick={() => handleDeleteLoanData(data.ID)}
                    >
                      Sil
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        ) : (
          <p>Kredi bavurusu bulunmuyor.</p>
        )}
        <Footer />
      </div>
    </div>
  );
};

export default BavurularmPage;
